:root {
  --lmseo-border-color: #38383a;
  --lmseo-footer-border: 1px solid var(--lmseo-border-color);
}
.footer {
  // background-color: $hexachromeBlack;

  color: $aluminum;
  // padding-top: 3.5rem;
  // padding-bottom: 6rem;
  padding: 0;
  border-bottom: 10px solid $blue;
  border-top: var(--lmseo-footer-border);
  .copyright {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .contact-me-wrapper {
    border-bottom: var(--lmseo-footer-border);
  }
  .contact-me {
    font-size: 1.4rem;
  }
  a {
    &:hover {
      color: $white;
      transition: color 0.3s ease-in;
    }
    text-decoration: none;
    i {
      color: black;
      transition: all 0.5s ease-out 0s;
    }
    &:hover svg {
      color: $white;
    }
  }
}

/* Footer Widgets
------------------------------------------------------------ */
.footer-social-wrapper {
}
.footer-social {
  width: 100%;
  // margin: 17px 0;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-evenly;
  list-style: none;
  a {
    padding: 0.5rem;
    border-radius: 50%;
    background-color: $aluminum;
    height: 4rem;
    width: 4rem;
    text-align: center;
    display: block;
    color: black;
    transition: all 0.5s ease-out 0s;
    position: relative;
  }
  svg {
    height: 3rem;
    width: 3rem;
  }
  .icon-instagram {
    &:hover {
      background-color: #3f729b;
      color: $white;
    }
  }
  .icon-facebook {
    &:hover {
      background-color: #3b5998;
      color: $white;
    }
  }
  .icon-twitter {
    &:hover {
      background-color: #55acee;
      color: $white;
    }
  }
  .icon-linkedin {
    &:hover {
      background-color: #0288d1;
      color: $white;
    }
  }
}

.footer-widgets-1,
.footer-widgets-2,
.footer-widgets-3 {
  padding: 2rem 0 2rem 0;
  border-top: var(--lmseo-footer-border);
}
.footer-widgets-3 {
  padding-bottom: 7rem;
}
.footer-widgets {
  color: $aluminum;
  line-height: 30px;
  font-size: rfs-value(14px);
  // border-top: 1px solid #626262;
  // padding: 3.5rem 0 0 0;
  padding: 0;
  // margin-top: 1.5rem;
  margin-top: 0;
  h4 {
    font-size: rfs-value(15px);
    color: $fosil;
    text-transform: uppercase;
    letter-spacing: inherit;
    font-weight: 400;
  }
  li {
    font-size: rfs-value(14px);
  }
  input[type="search"] {
    @extend .black-small-input-box;
    color: $white;
    min-width: 150px;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      display: block;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 4px;
      transform: scaleY(1);
      background-color: #ffec2d;
      transition: transform 0.2s;
      z-index: -1;
    }
  }
  input[type="submit"] {
    @extend %black-small-button;
    line-height: 1.3rem;
    padding-bottom: 9px;
    min-width: 70px;
  }
  a {
    color: #d3d3d3;
    font-weight: bold;
  }
  a:hover {
    color: $white;
  }
}

.tagcloud {
  // margin-bottom: 2rem;
  @include clearfix;
  a {
    width: 49%;
    display: block;
    float: left;
    padding: 5px;
  }
}
/*Individual Pages*/
/* Wrap
------------------------------------------------------------ */
.blog-index-class #wrap {
  margin: 0 auto 0;
}
.blog-index-class .wrap {
  margin: 0 auto;
}
/* Inner
------------------------------------------------------------ */

/* Taxonomy Description
------------------------------------------------------------ */
.taxonomy-description {
  margin: 0 0 30px;
  padding: 10px;
}
/* Images
------------------------------------------------------------ */
.blog-index-class img {
  height: auto;
  max-width: 100%;
}
/* Comments
------------------------------------------------------------ */
.blog-index-class #comments,
.blog-index-class #respond {
  font-size: 13px;
  margin: 0 0 15px;
  overflow: hidden;
}
.blog-index-class #comments p,
.blog-index-class #respond p {
  font-size: 13px;
}
.blog-index-class #author,
.blog-index-class #comment,
.blog-index-class #email,
.blog-index-class #url {
  font-size: 16px;
  margin: 10px 5px 0 0;
  padding: 5px;
  width: 250px;
}
.blog-index-class #comment {
  height: 150px;
  margin: 10px 0;
  width: 98%;
}
.blog-index-class .ping-list {
  margin: 0 0 40px;
}
.comment-list ol,
.ping-list ol {
  padding: 10px;
}
.blog-index-class .comment-list {
  list-style: none;
}
.blog-index-class .comment-list li,
.blog-index-class .ping-list li {
  font-weight: bold;
  margin: 15px 0;
  padding: 35px 35px 15px;
  text-shadow: $white 1px 1px;
}
.blog-index-class .comment-list li {
  list-style: none;
}
.blog-index-class .comment-list li ul li {
  background: $white;
  list-style-type: none;
}
.blog-index-class .comment-list p,
.blog-index-class .ping-list p {
  margin: 10px 5px 10px 0;
}
.blog-index-class .comment-list cite,
.blog-index-class .comment-list .says,
.blog-index-class .ping-list cite {
  color: #555;
  font-family: Verdana, Helvetica, Arial, Tahoma, sans-serif;
  font-size: 11px;
  font-style: normal;
  text-transform: uppercase;
}
.blog-index-class .commentmetadata {
  font-family: Verdana, Helvetica, Arial, Tahoma, sans-serif;
  font-size: 9px;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
}
.blog-index-class .comment-content {
  margin: 20px 0;
}
.blog-index-class .comment-reply-link {
  border-bottom: 5px solid #d05353;
  font-family: Verdana, Arial, Tahoma, sans-serif;
  font-size: 9px;
  padding: 0 8px 15px;
  text-decoration: none;
  text-transform: uppercase;
}
.blog-index-class .comment-reply-link:hover {
  border-bottom: 5px solid $jet;
  text-decoration: none;
}
.blog-index-class .nocomments {
  text-align: center;
}
.blog-index-class #comments .navigation {
  display: block;
}
