.avatar {
  margin: 10px;
  padding: 0;
  border: 0;
  border-radius: 50%;
}
.author-box {
  border-bottom: 1px solid $gainsboro;
  margin: 50px 0 50px 0;
  padding: 2rem 0 1.5rem 0;
  .avatar {
    float: left;
    margin: 20px;
  }
  .author-box-title {
  }
}
.author-box:before,
.author-box:after {
  content: " ";
  display: table;
}
.author-box:after {
  clear: both;
}
section.widget {
  padding: 1rem 0;
}
.widget_recent_entries {
  ul {
    margin: 0;
    padding: 0 0 0 13px;
  }
}
@include media-breakpoint-up(md) {
  .footer {
    .contact-me {
      font-size: var(--bs-body-font-size);
    }
  }

  .tagcloud {
    a {
      width: auto;
      display: inline-block;
      float: none;
      padding: 2px;
    }
  }
  .widget_search {
    width: 100%;
  }
}
@include media-breakpoint-up(lg) {
  .footer-top {
    border-bottom: var(--lmseo-footer-border);
  }
  .footer {
    .copyright-wrapper {
      border-top: var(--lmseo-footer-border);
    }
    .contact-me-wrapper {
      border-right: var(--lmseo-footer-border);
      border-bottom: none;
    }
  }
  .footer-widgets-1,
  .footer-widgets-2,
  .footer-widgets-3 {
    padding: 0;
    border-top: none;
  }
  .footer-widgets-1,
  .footer-widgets-2 {
    border-right: var(--lmseo-footer-border);
  }
  // .footer-widgets-1 {
  //   float: left;
  //   padding-right: 20px;
  //   width: 33%;
  // }
  // .footer-widgets-2 {
  //   padding-right: 20px;
