  //   width: 33%;
  // }
  // .footer-widgets-3 {
  //   padding-top: 30px;
  //   float: right;
  //   width: 33%;
  // }
  .footer-social {
    width: auto;
    a {
      padding: 0.5rem;
      border-radius: 50%;
      background-color: $aluminum;
      height: 2.5rem;
      width: 2.5rem;
      text-align: center;
      display: block;
      color: black;
      transition: all 0.5s ease-out 0s;
      position: relative;
      svg {
        height: 2rem;
        width: 2rem;
      }
    }
  }
  .inline-list > li {
    display: block;
    float: left;
    list-style: none;
    margin-left: 1.375rem;
  }
  .footer-widgets-1,
  .footer-widgets-2,
  .footer-widgets-3 {
    padding: 5rem 1.7rem 10rem;
  }
  .footer-widgets-1 {
    padding-left: 0;
  }
  .footer-widgets-3 {
    padding-right: 0;
    justify-content: center;
  }
}
